import React from 'react'
import Title from '../../../../../common/Title/Title'
import { MenuItem } from '@material-ui/core'
import MyTextField from '../../../../../common/MyTextField/MyTextField'
import bs from '../../../../../../utils/BrowserStorage'
import addresses from '../../../../../../utils/addresses'
import { spamModeType } from '../../../../../../types/types'
import { useFormikContext } from 'formik'

interface IProps {
  setPlaceholder: (placeholder: string) => void
}

function Select ({ setPlaceholder }: IProps) {
  const { setFieldValue } = useFormikContext()

  return (
    <>
      <Title> </Title>
      <MyTextField
        name="spamMode"
        fullWidth
        select
        variant="outlined"
        onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
          const spamMode = e.target.value as spamModeType

          bs.local.set('fields.spamMode', spamMode)
          setPlaceholder(addresses.getPlaceholder(spamMode as spamModeType))
          setFieldValue('addressees', addresses.getLocalValue(spamMode) || '')
        }}
      >
        <MenuItem value={'pm'}>  <span role="img" aria-label="pm"></span></MenuItem>,
        <MenuItem value={'talks'}> <span role="img" aria-label="talks"></span></MenuItem>,
        <MenuItem value={'talksAutoExit'}>   <span role="img" aria-label="talks"></span></MenuItem>,
        <MenuItem value={'usersWalls'}>  <span role="img" aria-label="usersWalls"></span></MenuItem>,
        <MenuItem value={'groupsWalls'}>  <span role="img" aria-label="groupsWalls"></span></MenuItem>,
        <MenuItem value={'comments'}> <span role="img" aria-label="comments"></span></MenuItem>,
        <MenuItem value={'discussions'}> <span role="img" aria-label="discussions"></span></MenuItem>
      </MyTextField>
    </>
  )
}

export default Select
