import { addLogItem, setSpamIsRun } from '../redux/spamer-reducer'
import Spamer from './Spamer'
import { IValues } from '../types/types'
import store from '../redux/store'

//     
function submit (values: IValues, setErrors: any, setFieldError: any, addLog: () => void) {
  const accounts = store.getState().accountsReducer.accounts

  // 
  if (!accounts.length) store.dispatch(addLogItem('   ', 'warning'))
  else if (!accounts.some(account => account.isEnabled)) store.dispatch(addLogItem('  ', 'warning'))
  if (!values.message && !values.attachment) {
    setErrors({
      message: '     ',
      attachment: '     '
    })
  }
  if (!values.addressees) setFieldError('addressees', '  ')

  //   
  if (
    (values.message || values.attachment) &&
    values.addressees &&
    accounts.length &&
    accounts.some(account => account.isEnabled)
  ) {
    store.dispatch(setSpamIsRun(true))
    addLog()

    new Spamer({
      ...values,
      attachment: values.attachment,
      addressees: values.addressees,
      message: values.message.split('\n').join('%0A')
    }).start()
  }

}

export default submit
